import React from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Header.css';
import image from './Pasha28198.jpg';

class Header extends React.Component {
  render() {
    return (
      <div className={s.header}>
        <div className={s.containerInf}>
          <div>
            <img src={image} alt="logo" />
          </div>
          <div className={s.fiowrapper}>
            <span className={s.fio}>Pavlo Gnydyn</span>
          </div>
        </div>
        <div className={s.nameCategory}>
          <span>
            {this.props.Category}
          </span>
        </div>
        <div className={s.visitGitWrapper}>
          <a href="https://github.com/Pasha28198" target="_blank" className={s.visitGit}>Visit My Git</a>
        </div>
      </div>
    );
  }
}

export default withStyles(s)(Header);



