import React from 'react';
import s from './Abilities.css';
import ListAbilities from '../../components/ListAbilities/ListAbilities';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import data from './dataFile.js';
import imageSkills from './translation.png';

class Abilities extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: 'skills',
    };
  }
  abilitiesBtn(tab) {
    if (tab != this.state.visible) {
      this.setState(() => ({
        visible: tab,
      }));
    }
  }
  render() {
    return (
      <div className={s.Abilities}>
        <div className={s.tabs}>
          <button
            onClick={() => {
              this.abilitiesBtn('skills');
            }}
            className={s.btnEdu}
          >
            Skills
          </button>
          <button
            onClick={() => {
              this.abilitiesBtn('language');
            }}
            className={s.btnCareers}
          >
            Language
          </button>
        </div>
        <ListAbilities
          data={this.state.visible == 'skills' ? data.skills : data.language}
        />
        {this.state.visible == 'language'
          ? <img className={s.ImageAbility} src={imageSkills} alt="" />
          : ' '}
      </div>
    );
  }
}

export default withStyles(s)(Abilities);



