import React from 'react';
import s from './LevelScills.css';
import withStyles from 'isomorphic-style-loader/lib/withStyles';

class ListAbilities extends React.Component {
  render() {
    return (
      <div className={s.wrapperLevel}>
        {this.props.count.map((item, idx) => {
          if (item) {
            return <div className={s.withColor} />;
          }
          return <div className={s.emptyColor} />;
        })}
      </div>
    );
  }
}

export default withStyles(s)(ListAbilities);



