import React from 'react';
import s from './ListAbilities.css';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import LevelScills from '../LevelScills/LevelScills';

class ListAbilities extends React.Component {
  render() {
    function toArray(num) {
      const arr = [];
      for (let i = 0; i < 5; i++) {
        if (num > 0) {
          arr.push(1);
          num--;
        } else {
          arr.push(0);
        }
      }
      return arr;
    }

    return (
      <div
        className={
          typeof this.props.data[0].value === 'number'
            ? s.wrapperAbili
            : s.wrapperLang
        }
      >
        {this.props.data.map((item, idx) =>
          <div className={s.wrapItem} key={idx}>
            <div className={s.leftName}>
              {item.name}
            </div>
            <div className={s.rightDescriptionAbilities}>
              {typeof item.value === 'number'
                ? <LevelScills count={toArray(item.value)} />
                : <span className="levelLang">
                    {item.value}
                  </span>}
            </div>
          </div>,
        )}
      </div>
    );
  }
}

export default withStyles(s)(ListAbilities);



