import React from 'react';
import Abilities from './Abilities';
import Layout from '../../components/Layout';

const title = 'Abiliti';

function action() {
  return {
    chunks: ['abiliti'],
    title,
    component: (
      <Layout Header="Abilities">
        <Abilities />
      </Layout>
    ),
  };
}

export default action;



